* {
	user-select: none;
	/* standard syntax */
	-webkit-user-select: none;
	/* webkit (safari, chrome) browsers */
	-moz-user-select: none;
	/* mozilla browsers */
	-khtml-user-select: none;
	/* webkit (konqueror) browsers */
	-ms-user-select: none;
	/* IE10+ */
}

*.dropdown-toggle.btn.btn-transparent::after {
	content: none;
}

canvas {
	z-index: -99;
}